import React from 'react';
import DataFetcher from './DataFetcher'; // Update the path based on your project structure
import logo from './logo.svg';
import './App.css';

// Import your components
import Button from './components/Button';
import Form from './components/Form';
import Card from './components/Card';

function App() {
  // Define event handlers
  const handleButtonClick = () => {
    alert('Button clicked!');
  };

  const handleFormSubmit = (inputValue) => {
    alert(`Form submitted with value: ${inputValue}`);
  };

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />

        {/* Use the DataFetcher component */}
        <DataFetcher />

        {/* Use the Button component */}
        <Button onClick={handleButtonClick} label="Click me" />

        {/* Use the Form component */}
        <Form onSubmit={handleFormSubmit} />

        {/* Use the Card component */}
        <Card title="Sample Card" content="This is some card content." />

        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}

export default App;
